import * as React from 'react';

type Props = {
  width?: string;
  height?: string;
};

// https://unblast.com/free-veterinarian-vector-illustration-ai/
export const VeterinarianLogo: React.FC<Props> = (props) => {
  const { width, height } = props;
  const imageData =
    'data:image/png;base64,';

  return <img width={width} height={height} src={imageData} alt="VeterinarianLogo" />;
};

export default { VeterinarianLogo };
